.sp {
  padding-top: 15vh;
  padding-bottom: 15vh;
}

footer {
  color: #fff;
  background-image: linear-gradient(-140deg, #ee5792 0%, #6d5e89 100%);
}

.major {
  font-weight: 700;
}

.semi {
  font-size: 13px;
  margin-top: 40px;
}

.quick-link li {
  margin: 0;
  padding: 0;
}

.quick-link li a {
  color: #fff;
}

.wf-active h1,
.wf-active h2,
.wf-active h3,
.wf-active h4,
.wf-active h5,
.wf-active h6 {
  font-family: "Roboto Slab", serif;
  font-weight: 700;
}

.textwidget a {
  color: #fff;
}
.textwidget p {
  font-family: "Mulish", sans-serif;
}
.social2 {
  padding: 0.2vw;
  float: left;
  opacity: 0.8;
  width: 30px;
  height: 30px;
  filter: invert(100%);
  -moz-transition-property: all;
  -o-transition-property: all;
  -webkit-transition-property: all;
  transition-property: all;
  -moz-transition-duration: 0.5s;
  -o-transition-duration: 0.5s;
  -webkit-transition-duration: 0.5s;
  transition-duration: 0.5s;
}

p {
  font-family: "Muli", sans-serif;
}

.legal a {
  color: #fff;
  text-decoration: underline;
}

p {
  margin-top: 0;
  margin-bottom: 1rem;
}
