.select-container {
  display: inline-block;
  min-width: 250px;
  position: relative;
}

.selected-text {
  background-color: #fff;
  border: 1px solid #ced4da;
  border-radius: 4px;
  padding: 0.375rem 0.75rem;
  cursor: pointer;
}

.selected-text.placeholder-style {
  color: #6c757d;
}

.selected-text::after {
  content: "";
  position: absolute;
  right: 10px;
  top: 16px;
  border: 7px solid transparent;
  border-color: #b1b1b1 transparent transparent transparent;
}

.selected-text.active::after {
  top: 8px;
  border-color: #cc242400 #ff2d2d00 #b1b1b1 transparent;
}

.select-options {
  position: absolute;
  width: 100%;
  margin: 0;
  padding: 0;
  z-index: 1;
  border: 1px solid #ddd;
  border-top: 0;
  border-bottom: 0;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}

.select-options li {
  list-style-type: none;
  padding: 0.375rem 0.75rem;
  background: #f7f7f7;
  border-bottom: 1px solid #dedede;
  cursor: pointer;
}
.select-options li:last-child {
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}
.select-options li:hover {
  background-color: #e55891;
  color: #ffffff;
}
