.button {
  padding: 10px 30px;
  border: none;
  font-size: 18px;
  cursor: pointer;
  outline: none !important;
  border-radius: 6px;
  color: #fff;
  min-width: 100px;
}

.button:disabled {
  cursor: not-allowed;
  pointer-events: none;
}

.button:hover {
  background-color: rgb(255 167 206);
}

.prev-next {
  background-color: rgb(255 167 206);
}

.prev-next:hover {
  background-color: rgb(224 96 153);
}

.submit {
  background-color: #007bff;
}

.submit:hover {
  background-color: #0069d9;
}
