.details-container {
  display: flex;
  flex-direction: column;
  padding: 20px;
  box-shadow: 0px 0px 20px #00000020;
  border-radius: 8px;
  background-color: white;
  margin: 70px 0;
}

.next-button {
  float: right;
  padding: 10px 30px;
  border: none;
  outline: none;
  background-color: rgb(255 167 206);
  font-size: 18px;
  cursor: pointer;
  border-radius: 6px;
  width: 10%;
  align-self: flex-end;
  min-width: 100px;
}

.next-button:hover {
  background-color: #ee5792;
}

.next-button:focus {
  border: none;
  outline: none;
}

@media only screen and (max-width: 767px) {
  .details-container {
    margin: 15px 0;
  }
}
