.report-container {
  display: flex;
  flex-direction: column;
  box-shadow: 0px 0px 20px #00000020;
  border-radius: 8px;
  background-color: white;
  margin: 70px 0;
}

.report-heading {
  margin-bottom: 25px;
  background-color: #dd5990;
  color: #fff;
  padding: 20px;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  font-weight: 600;
  font-size: 25px;
}

.report-content {
  padding: 0 20px;
}

.report-container h4 {
  border-bottom: 2px solid #df5891;
  padding-bottom: 15px;
  margin-bottom: 25px;
  font-size: 18px;
}

.child-details {
  margin-bottom: 25px;
}

.child-details-group {
  margin-bottom: 20px;
}

.child-label {
  min-width: 150px;
  display: inline-block;
}

.child-value {
  margin-left: 10px;
}

.child-findings {
  margin-bottom: 25px;
}

.child-findings li:not(:last-child) {
  margin-bottom: 15px;
}
@media only screen and (max-width: 767px) {
  .child-label {
    min-width: 125px;
    display: inline-block;
  }
  .report-container {
    margin: 15px 0;
  }
}

.button_container {
  display: flex;
  padding: 10px;
  justify-content: center;
}
.button_container .next-button {
  padding: 10px 30px;
  border: none;
  outline: none;
  background-color: rgb(255 167 206);
  font-size: 18px;
  cursor: pointer;
  border-radius: 6px;
  width: unset;
}