.main-header-div {
  width: 100%;
  background: #fff;
  border-bottom: 1px solid #dfdfdf;
  padding: 5px;
}

.main-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.main-logo {
  max-width: 161px;
  display: inline-block;
}

.main-logo a {
  display: block;
}

.main-logo img {
  width: 100%;
  height: auto;
}

.contact-info-section {
  display: flex;
  justify-content: space-between;
}

.phone-number-section {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-right: 100px;
}

.phone-number-section > span {
  font-size: 14px;
}

.phone-number-section > span:first-child {
  margin-right: 10px;
}

.link-icons {
  display: inline-flex;
}

.link-icons a {
  display: block;
}

.link-icons img {
  padding: 0.3vw;
  opacity: 0.5;
  width: 26px;
  height: 26px;
  -moz-transition-property: all;
  -o-transition-property: all;
  -webkit-transition-property: all;
  transition-property: all;
  -moz-transition-duration: 0.5s;
  -o-transition-duration: 0.5s;
  -webkit-transition-duration: 0.5s;
  transition-duration: 0.5s;
}

.link-icons img:hover {
  opacity: 1;
  cursor: pointer;
}

.navbar {
  padding: 8px 0 !important;
}

.btn-booking {
  background: #f28b00;
  border-radius: 50px;
  border: none;
  color: #fff;
}

.book-image {
  width: 27px;
  height: 27px;
  margin-right: 5px;
}

.nav-item {
  font-family: "Roboto Slab", serif;
  color: #000;
  opacity: 1 !important;
}

.nav-item:hover {
  opacity: 0.7 !important;
}

.navbar-light .navbar-nav .nav-link {
  color: #000 !important;
  font-size: 16px;
}

.sub-header {
  background-color: #f8f9fa !important;
}

@media only screen and (min-width: 768px) and (max-width: 991px) {
  .phone-number-section > span:nth-child(2),
  .link-icons {
    display: none;
  }
}

@media only screen and (max-width: 767px) {
  .contact-info-section {
    display: none;
  }
  section {
    background-color: #fff;
    padding: 20px 0;
  }
}
